@import url('https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:wght@100;200;300;400;500;600;700;800;900&display=swap');

html {
    scroll-behavior: smooth;
}

body {
    margin: 0;
    font-family: 'Be Vietnam Pro', sans-serif;
    overflow-x: hidden;
}

/* style.css */
.zoom-fade-animation {
    animation: zoomFadeOut;
    animation-duration: 1s; /* Animáció időtartama */
    animation-timing-function: ease-in-out; /* Animáció időzítése (lelassulás) */
    animation-fill-mode: forwards; /* Az animáció befejezése után az utolsó állapotot tartja meg */
    animation-delay: 0.7s; /* Késleltetés az animáció elindulása előtt (500ms) */ /* Kezdeti átlátszatlanság */
    transform: scale(1.3); /* Kezdeti méretnagyítás */
  }
  
  @keyframes zoomFadeOut {
    0% {
        transform: scale(1.3); /* Normál méret a kezdeti állapotban */
    }
    50% {
        transform: scale(1.3);
    }
    100% {
      transform: scale(1); /* A végső méret a nagyításhoz */
    }
  }

.navbar {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    z-index: 2;
}    

.navitems {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.navitems li {
    list-style: none;
}

.navitems li a{
    text-decoration: none;
    color: #FFFFFA;
    font-size: 20px;
    font-weight: 400;
}

.reservbtn{
    width: 300px;
    height: 100px;
    background-color: #080708;
    padding: 15px 50px;
    border-radius: 37px;
    color: #FFFFFA;
}

.landing-container {
    width: 100%;
    height: 100vh;
    background-image: url(../public/landingbg.png);
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 1;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 50px;
}

.text {
    display: flex;
    flex-direction: column;
    gap: 0px;
}

.text p {
    width: 650px;
    color: #FFFFFA;
    font-size: 32px;
    font-style: normal;
    font-weight: 200;
    line-height: normal;
    padding-left: 20px;
    opacity: 0;
}

.landing-container h1 {
    color: rgba(0, 0, 0, 0.17);
    font-size: 350px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    -webkit-text-stroke-width: 7px;
    -webkit-text-stroke-color: #FFFFFA;
    margin: 0;
    line-height: 0.8;
}

.tracking-in-expand{animation-delay: 2s; animation:tracking-in-expand .7s cubic-bezier(.215,.61,.355,1.000) both}
  @keyframes tracking-in-expand {
    0% {
        letter-spacing:-.5em;
        opacity:0
    }
    40% 
    {
        opacity:.6
    
    }
    
    100% {
        opacity:1
    }
    }

    .text-focus-in{animation-delay: 2s; animation:text-focus-in 1s cubic-bezier(.55,.085,.68,.53) both}
    @keyframes text-focus-in{0%{filter:blur(12px);opacity:0}100%{filter:blur(0);opacity:1}}

    .slide-right{animation:slide-right .5s cubic-bezier(.25,.46,.45,.94) both}
    @keyframes slide-right{0%{transform:translateX(-600px)}100%{transform:translateX(0px)}}

    .slide-top{animation: slide-top 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 3s both;}
    @keyframes slide-top {
        0% {
            opacity: 0;
            transform: translateY(100px);
        }
        100% {
            opacity: 1;
            transform: translateY(0px);
        }
      }
      
.about-us {
    width: 100%;
    height: 50vh;
    background-color: #FFFFFA;
}      

.wrapper {
    display: flex;
    align-items: first baseline;
    width: 100%;
    justify-content: space-between;
    padding: 0px 70px;
    box-sizing: border-box;
}

.leftwrapper p{
    color: #121212;
    font-size: 32px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
    letter-spacing: -1.44px;
}

.rightwrapper {
    width: 650px;
}

.rightwrapper h2 {
    color: #080708;
    font-size: 40px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: -1.8px;
    opacity: 0.8;
}

.mobile-navbar {
    display: none;
    position: absolute;
    z-index: 2;
    width: 100%;
}

.hamburger {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 8px;
    width: 100%;
    height: 100px;
    padding: 30px;
    box-sizing: border-box;
    position: absolute;
    z-index: 3;
    justify-content: center;
    cursor: pointer;
}

.hamline {
    height: 1px;
    width: 50px;
    background-color: #FFFFFA;
}

.hamline:nth-child(2) {
    width: 30px;
}

.hamline:nth-child(3) {
    width: 40px;
}

.mobilenavitems {
    background-color: #FFFFFA;
    height: 700px;
    position: absolute;
    width: 100%;
    z-index: 2;
    display: none;
}

.active {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.mobilenavitems ul{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 50px;
    padding-left: 0;
}

.mobilenavitems li {
    list-style: none;
}

.mobilenavitems li a {
    text-decoration: none;
    color: #080708;
}

.mobilereservbtn {
    width: 300px;
    height: 100px;
    background-color: #080708;
    padding: 15px 50px;
    border-radius: 37px;
    color: #FFFFFA;
}

.slide-bottom{animation:slide-bottom .5s cubic-bezier(.25,.46,.45,.94) both}
@keyframes slide-bottom{0%{transform:translateY(-600px)}100%{transform:translateY(0px)}}

.menuactive span:nth-child(1) {
    transform: rotate(-46deg);
    background-color: var(--bg);
    transition: all 0.35s ease-in;
}

.menuactive span:nth-child(2) {
    opacity: 0;
    transition: all 0.05s ease-in;
}

.menuactive span:nth-child(3) {
    transform: rotate(46deg);
    position: relative;
    top: -15px;
    transition: all 0.35s ease-in;
}

.slidercontainer {
    width: 100%;
    height: 400px;
    position: relative;
    overflow: hidden;
    margin-left: 70px;
    margin-right: 70px;
    &.slideractive {
		cursor: grab;
	}
  }

.product-slider {
    width: 100%;
    height: 400px;
    display: flex;
    align-items: center;
    padding-bottom: 50px;
	overflow: hidden;
	white-space: nowrap;
	cursor: pointer;
    max-width: 100%;
}

.card {
    width: min-content;
    display: inline-block;
    overflow: hidden;
    padding-right: 27px;
}

.card-content {
    position: relative;
}

.card img {
    border-radius: 10px;
    position: relative;
    z-index: 1;
    width: 60vh;
}

.slider-section {
    padding-bottom: 100px;
}

.btn {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 50px;
}

.btn button {
    border-radius: 43px;
    background: #080708;
    border: none;
    font-family: 'Be Vietnam Pro', sans-serif;
    font-size: 24px;
    width: 300px;
    height: 60px;
    color: #FFFFFA;
}

.location-container {
    width: 100%;
    height: 80vh;
    background: #080708;
    border-radius: 50px 50px 0 0;
    padding: 90px 80px;
    display: flex;
}

.wrapper-about {
    width: 80%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.location-title {
    font-size: 48px;
    color: #FFFFFA;
    font-weight: 300px;
    padding-bottom: 40px;
    margin: 0;
}

.wrapper-about h2 {
    font-size: 64px;
    color: #FFFFFA;
    font-weight: 500px;
    max-width: 70%;
    margin: 0;
    padding-bottom: 10px;
}
.location-desc {
    font-size: 32px;
    color: #FFFFFA;
    font-weight: 400px;
    max-width: 60%;
    margin: 0;
    padding-bottom: 80px;
}

.wrapper-about button {
    width: 50%;
    height: 10%;
    border: none;
    border-radius: 70px;
    background: #FFFFFA;
    font-family: "Be Vietnam Pro";
    font-size: 24px;
}

.location-image {
    width: 50%;
    height: 100vh;
}

.comm {
    width: 100%;
    height: 80vh;
}

.community-slider {
    padding-left: 60px;
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 40px;
}

.card-shadow {
    margin-left: 55%;
    width: 400px;
    height: 450px;
    transform: rotate(20deg);
    background: rgb(0,0,0);
    background: linear-gradient(90deg, rgba(217, 217, 217,0.7)
    0.5%, rgba(255, 255, 250, 1)20%);
    position: relative;
    z-index: 1;
}

.commcard-content {
    position: absolute;
    z-index: 2;
    padding: 40px;
    width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 200px;
}

.stars {
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 5px;
    position: relative;
    left: 66%;
    top: 10%;
    z-index: 2;
}

.community-section {
    width: 100%;
    height: 35vh;
    background-color: #FFFFFA;
}

.commcard-content h1 {
    color:  #121212;
    font-size: 40px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: -1.8px;
    margin: 0;
}

.commcard-content p {
    color: #121212;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: -1.08px;
    opacity: 0.8;
    margin: 0;
}

.community-card {
    width: 550px;
    height: 300px;
    border-radius: 34px;
    overflow: hidden;
    background: #FFFFFA;
    box-shadow: 8px 20px 48px 0px rgba(0, 0, 0, 0.10), 33px 81px 87px 0px rgba(0, 0, 0, 0.09), 75px 181px 118px 0px rgba(0, 0, 0, 0.05), 133px 322px 140px 0px rgba(0, 0, 0, 0.01), 209px 504px 153px 0px rgba(0, 0, 0, 0.00);
}

.addItem {
    width: 100px;
    height: 100px;
    background-color: #FFFFFA;
    border-radius: 50%;
    display: flex;
    align-items: center;
    margin-left: 60px;
    justify-content: center;
    position: relative;
    z-index: 4;
    cursor: pointer;
    top: -20px;
    box-shadow: 8px 20px 48px 0px rgba(0, 0, 0, 0.10), 33px 81px 87px 0px rgba(0, 0, 0, 0.09), 75px 181px 118px 0px rgba(0, 0, 0, 0.05), 133px 322px 140px 0px rgba(0, 0, 0, 0.01), 209px 504px 153px 0px rgba(0, 0, 0, 0.00);
}

.addItem img {
    width: 35%;
}

.modal {
    width: 1000px;
    height: 650px;
    background: #121212;
    position: relative;
    z-index: 5;
    top: -700px;
    left: 22%;
    backdrop-filter: blur(100px);
    border-radius: 32px;
}

.modal-content {
    width: 1000px;
    height: 600px;
    padding: 50px;
}

.modal-title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 900px;
    margin-bottom: 50px;
}

.modal-title h1 {
    margin: 0;
    color: #FFFFFA;
}

.modal button {
    width: 150px;
    height: 50px;
    background-color: #FFFFFA;
    color: #121212;
    font-family: "Be Vietnam Pro";
    font-size: 20px;
    border: none;
    border-radius: 24px;
    padding: 0px 20px;
    cursor: pointer;
}

.modal-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.modal-form input {
    width: 500px;
    height: 50px;
    background-color: #FFFFFA;
    border: none;
    font-family: "Be Vietnam Pro";
    font-size: 16px;
    padding-left: 20px;
    box-sizing: border-box;
    border-radius: 14px;
}

.modal-form label {
    color: #FFFFFA;
    font-size: 24px;
}

.submitbtn  {
    margin-top: 20px;
}

@media only screen and (max-width: 600px) {
    .navbar {
        display: none;
    }

    .mobile-navbar {
        display: block;
    }

    .text {
        box-sizing: border-box;
        margin-top: 150px;
    }

    .landing-container {
        height: 85vh;
        justify-content: flex-start;
    }

    .landing-container h1 {
        font-size: 90px;
        -webkit-text-stroke-width: 2px;
    }

    .text p {
        font-size: 16px;
        width: 300px;
        padding-left: 0;
    }

    .rightwrapper {
        width: 200px;
    }

    .rightwrapper h2 {
        font-size: 20px;
        width: 300px;
    }

    .about-us {
        position: relative;
        top: -160px;
        height: 15vh;
    }

    .wrapper {
        padding: 0 50px;
        flex-wrap: wrap;
        flex-direction: column;
    }

    .slidercontainer {
        margin-left: 50px;
        margin-right: 50px;
        height: 180px;
    }

    .product-slider {
        height: 200px;
        padding-bottom: 0;
    }

    .card img {
        width: 30vh;
    }

    .btn {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .btn button {
        border-radius: 43px;
        background: #080708;
        border: none;
        font-family: 'Be Vietnam Pro', sans-serif;
        font-size: 16px;
        width: 150px;
        height: 30px;
        color: #FFFFFA;
    }
}